<template>
  <div style="display: block; margin: auto; text-align: center;">
    <div>经登陆成功了，您的用户名为：{{ user_name }}, 欢迎！</div>

    <div class="mdui-drawer-body-left" style="padding-top: 20px;"></div>

    <div class="mdui-card, mdui-ripple each_card">
      <p>房间1的温度</p>
      <button class="mdui-btn mdui-color-pink-accent" @click="rander_echarts">渲染图表</button>
      <button class="mdui-btn mdui-color-pink-accent" @click="modi_echarts_data">+1s</button>
      <div id="echarts_1" style="width: 300px; height:300px;"></div>
    </div>

    <div class="mdui-card, mdui-ripple each_card">
      <p>房间2的温度</p>
      <button class="mdui-btn mdui-color-pink-accent" @click="rander_echarts">渲染图表</button>
      <button class="mdui-btn mdui-color-pink-accent" @click="modi_echarts_data">+1s</button>
      <div id="echarts_2" style="width: 300px; height:300px;"></div>
    </div>

    <div class="mdui-card, mdui-ripple each_card">
      <p>房间3的温度</p>
      <button class="mdui-btn mdui-color-pink-accent" @click="rander_echarts">渲染图表</button>
      <button class="mdui-btn mdui-color-pink-accent" @click="modi_echarts_data">+1s</button>
      <div id="echarts_3" style="width: 300px; height:300px;"></div>
    </div>

  </div>
</template>

<script>
export default {
  name: 'dashbord',
  data () {
    return {
      user_name: 'Lee',
      option: {
        title: {
          text: 'ECharts 入门示例'
        },
        tooltip: {},
        legend: {
          data: ['销量']
        },
        xAxis: {
          data: ['衬衫', '羊毛衫', '雪纺衫', '裤子', '高跟鞋', '袜子']
        },
        yAxis: {},
        series: [{
          name: '销量',
          type: 'bar',
          data: [5, 20, 36, 10, 10, 20]
        }]
      },
      flag: 2,
      ajax_data: undefined
    }
  },
  methods: {
    rander_echarts: function () {
      var myChart1 = this.echarts.init(document.getElementById('echarts_1'))
      var myChart2 = this.echarts.init(document.getElementById('echarts_2'))
      var myChart3 = this.echarts.init(document.getElementById('echarts_3'))
      myChart1.setOption(this.option)
      myChart2.setOption(this.option)
      myChart3.setOption(this.option)
    },
    modi_echarts_data: function () {
      var self = this
      this.mdui.JQ.ajax({
        method: 'GET',
        url: 'http://127.0.0.1:5000/api/test?p1=hello',
        success: function (data) {
          var result = JSON.parse(data)
          let num = result.data.data2
          console.log('hello')
          self.option.series[0].data[0] = num
          self.rander_echarts()
        }
      })
    }
  },
  mounted: function () {
    console.log('hello 页面加载完成后，就会调用我！')
  }
}
</script>

<style scoped>
  .each_card {
    width: 400px;
    height: 400px;
    margin: auto;
    display: inline-block;
  }
</style>



