<template>
  <div>
    <div style="height: 100px"></div>
    <!--    登录框-->
    <div class="mdui-card" style="display: block" id="login_box">
      <div class="mdui-card-header">
        <div class="card-header">
          <div class="card-inner">
            <h2 style="color: gray">欢迎登陆</h2>
          </div>
        </div>
        <div class="mdui-divider"></div>

        <div class="mdui-card-media">
          <img style="width: 60px; height: 60px; margin: 20px auto 20px auto;" src="/static/image/default_header.jpg"/>
        </div>

        <div id="login_box_container">
          <input class="mdui-textfield-input custom_row_spacing" type="text" placeholder="请输入用户名" v-model="username">
          <input class="mdui-textfield-input custom_row_spacing" type="password" placeholder="请输入密码" v-model="password">
          <button class="mdui-btn mdui-color-theme-accent mdui-ripple custom_row_spacing" style="width: 300px;" @click="submit">登陆</button>
          <label class="mdui-checkbox custom_row_spacing">
            <input type="checkbox" v-model="remember_flag"/>
            <i class="mdui-checkbox-icon"></i>
            <span style="font-size: small; color: gray;">记住密码</span>
          </label>
        </div>
      </div>
    </div>
    <div style="margin: 30px 0 30px 0; text-align: center;">
      <button class="mdui-btn" @click="forget_password">忘记密码</button>
      <button class="mdui-btn" @click="register_id">注册账号</button>
      <button class="mdui-btn" @click="check_cookies">测试cookie</button>
    </div>
    <!--    对话框-->
    <div class="mdui-dialog" id="password_error_dialog">
      <div class="mdui-dialog-title">您的密码似乎有误</div>
      <div class="mdui-dialog-content">请检查您键入的密码是否有误</div>
      <div class="mdui-dialog-actions mdui-dialog-actions-stacked">
        <button class="mdui-btn mdui-ripple" @click="close_dialog">好的</button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'login',
  data () {
    return {
      username: undefined,
      password: undefined,
      remember_flag: true,
      dialog_inst: undefined
    }
  },
  methods: {
    check_cookies: function () {
      this.mdui.JQ.ajax({
        beforeSend: function (xhr) {},
        method: 'GET',
        url: 'http://127.0.0.1:5000/api/check_cookie/',
        success: function (data) {
          console.log(data)
        }
      })
    },
    to_login_page: function () {
      if (this.username === 'admin' && this.password === 'admin') {
        this.to_dashboard_page()
      } else {
        console.log(this.remember_flag)
        this.dialog_inst = new this.mdui.Dialog('#password_error_dialog', {closeOnCancel: true})
        this.dialog_inst.open()
      }
    },
    close_dialog: function () {
      this.dialog_inst.close()
    },
    // 跳转函数
    to_dashboard_page: function () {
      window.location.href = '/#/dashboard'
    },
    forget_password: function () {
      console.log('忘记密码')
    },
    register_id: function () {
      window.location.href = '/#/register_user'
    },
    submit: function () {
      this.mdui.JQ.ajax({
        method: 'POST',
        url: '/api/login/',
        data: {
          username: this.username,
          password: this.password
        },
        success: function (data) {
          console.log(data)
        }
      })
    }
  },
  mounted: function () {
  }
}
</script>

<style scoped>
  #login_box {
    margin: auto;
    width: 500px;
    height: 450px;
    text-align: center;
    display: block;
    color: black;
    font-size: larger;
  }

  #login_box_container {
    width: 80%;
    height: 90%;
    margin: auto;
  }

  .custom_row_spacing {
    margin: 20px 0 20px 0;
  }
</style>



