<template>
  <div>
    <div style="height: 100px"></div>
    <!--    登录框-->
    <div class="mdui-card" style="display: block" id="login_box">
      <div class="mdui-card-header">
        <div class="card-header">
          <div class="card-inner">
            <h2 style="color: gray">欢迎注册</h2>
          </div>
        </div>
        <div class="mdui-divider"></div>

        <div class="mdui-card-media">
          <img style="width: 60px; height: 60px; margin: 20px auto 20px auto;" src="/static/image/default_header.jpg"/>
        </div>

        <div id="login_box_container">
          <input class="mdui-textfield-input custom_row_spacing" type="text" placeholder="昵称" v-model="nick_name">
          <input class="mdui-textfield-input custom_row_spacing" type="text" placeholder="用户名" v-model="username">
          <input class="mdui-textfield-input custom_row_spacing" type="password" placeholder="设置密码" v-model="password">
          <input class="mdui-textfield-input custom_row_spacing" type="password" placeholder="重复密码" v-model="password_2">
          <button class="mdui-btn mdui-color-theme-accent mdui-ripple custom_row_spacing" style="width: 300px;" @click="to_register_page">确认注册</button>
        </div>
      </div>
    </div>
    <button class="mdui-btn mdui-ripple custom_row_spacing" style="width: 300px;" @click="to_login_page">已经有账号了？去登陆</button>

    <!--    对话框-->
    <div class="mdui-dialog" id="password_error_dialog">
      <div class="mdui-dialog-title">请检查输入</div>
      <div class="mdui-dialog-content"> {{ this.dialog_info }} </div>
      <div class="mdui-dialog-actions mdui-dialog-actions-stacked">
        <button class="mdui-btn mdui-ripple" @click="close_dialog">好的</button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'register_user',
  data () {
    return {
      nick_name: undefined,
      username: undefined,
      password: '',
      password_2: '',
      dialog_inst: undefined,
      dialog_info: ''
    }
  },
  methods: {
    to_register_page: function () {
      console.log('确认按钮回调')
      let flag = this.check_valid()
      if (flag === 'error_code_1') {
        this.dialog_info = '您输入的密码长度太短'
        this.dialog_inst.open()
      }
      if (flag === 'error_code_2') {
        this.dialog_info = '两次输入的密码必须一致'
        this.dialog_inst.open()
      }
      if (flag === 'check_valid_success') {
        console.log('发起ajax请求，注册')
        console.log(this.username, this.password, this.nick_name)
      }
    },
    check_valid: function () {
      if (this.password.toString().length < 6 && this.password_2.toString().length < 6) {
        return 'error_code_1'
      }
      if (this.password !== this.password_2) {
        return 'error_code_2'
      }
      return 'check_valid_success'
    },
    close_dialog: function () {
      this.dialog_inst.close()
    },
    to_login_page: function () {
      window.location.href = '/#/'
    }
  },
  mounted: function () {
    this.dialog_inst = new this.mdui.Dialog('#password_error_dialog', {closeOnCancel: true})
  }
}
</script>

<style scoped>
  #login_box {
    margin: auto;
    width: 500px;
    height: 500px;
    text-align: center;
    display: block;
    color: black;
    font-size: larger;
  }

  #login_box_container {
    width: 80%;
    height: 90%;
    margin: auto;
  }

  .custom_row_spacing {
    margin: 20px 0 20px 0;
  }
</style>



